.form {
  width: 403px;
  min-height: 455px;
  padding: 25px 50px;

  color: $color-white;
  background-color: $color-tangaroa;
}

.form h3 {
  margin-bottom: 27px;
  font-size: 18px;
  line-height: 28px;
  font-weight: 500;
}

.form__field {
  margin-bottom: 21px;

  &--agreement {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
  }
}

.form input {
  width: 100%;
  padding: 13px 16px;
  font-size: 14px;
  background-color: $color-white;
  border: none;
  outline: none;

  &::placeholder {
    color: $color-tangaroa-70;
  }

  &[type="submit"] {
    font-weight: 600;
    cursor: pointer;
  }
}

.form__field--agreement input {
  width: 20px;
}

.form__field--agreement label {
  position: relative;
  top: -2px;
  padding-left: 23px;
  font-size: 10px;
}

.form__field--agreement input + label::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: $color-white;
}

.form__field--agreement input:checked + label::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 3px;
  width: 18px;
  height: 18px;
  background-image: url("../img/blocks/booking/booking-checked.svg");
  background-repeat: no-repeat;
}
